<template>
	<div class="tooltip">
	<div class="company-sale sale">
		<div class="sale-head">
			<div class="sale-title"> {{discount.discount}} %</div>
			<div class="sale-lacks">  {{ostatok}}   {{discount.next}} %</div>
		</div>
		
		<div class="sale-progressbar-wrap">
			<div class="sale-progressbar tooltip">
				
					
				<div class="sale-progressbar-line" :style="'width: '+ (progressInPercent < 15 ? 15 : progressInPercent) +'%'">
					<div class="sale-progressbar-val">
						<div>
							<span class="sale-progressbar-val-money">({{spent.toFixed(2).toLocaleString()}})</span>
							<span class="sale-progressbar-val-percent">{{progressInPercent}}%</span>
						</div>
					</div>

				</div>
		
			</div>
			<div class="sale-progressbar-money">
				<div class="sale-progressbar-min">{{discount.progressMoneyMin.toLocaleString()}}</div>
				<div class="sale-progressbar-max">{{discount.progressMoneyMax.toLocaleString()}}</div>
			</div>
		</div>
			
		</div>
					<span class="tooltiptext">
						<table>
							<tr><th> ,</th><th>,%</th></tr>
							<tr><td> 3  5 .</td><td>12.5</td></tr>
							<tr><td> 5  8 .</td><td>13.5</td></tr>
							<tr><td> 8  15 .</td><td>14.4</td></tr>
							<tr><td> 15 .</td><td>16.3</td></tr>
						</table>
					</span>
	</div>
</template>

<script>
import { ref,computed } from 'vue';

export default {
	props:{
		spent: { //   
			type: Number
		}
	},
	setup(props){
		
		const discountList = [
			{
				progressMoneyMin: 0,	//    
				progressMoneyMax: 3000000,	//    
				discount: 0,				//  
				next: 12.5					//  
			
			},
			{
				progressMoneyMin: 3000000,	//    
				progressMoneyMax: 5000000,	//    
				discount: 12.5,				//  
				next: 13.5					//  
			},
			{
				progressMoneyMin: 5000000,	//    
				progressMoneyMax: 8000000,	//    
				discount: 13.5,				//  
				next: 14.4					//  
			},
			{
				progressMoneyMin: 8000000,	//    
				progressMoneyMax: 15000000,	//    
				discount: 14.4,				//  
				next: 16.3					//  
			},
			{
				progressMoneyMin: 15000000,	//    
				progressMoneyMax: Infinity,	//    
				discount: 16.3,				//  
				next: 0					//  
			}
		]

		let discount = ref({});

		discount = computed(() => {
			let d = discountList.filter(v => parseFloat(props.spent) >= parseFloat(v.progressMoneyMin) 
													&& parseFloat(props.spent) < parseFloat(v.progressMoneyMax));
			return d.length > 0 ? d[0] : discountList[0];
		})
	

		return{
			discount,
			progressInPercent: computed(() => ((props.spent/discount.value.progressMoneyMax) * 100).toFixed(2)),//   
			ostatok: computed(() => ((discount.value.progressMoneyMax - props.spent).toFixed(2).toLocaleString())),
		}
	}
}

</script>

<style lang="sass" scoped>


</style>